body {
  margin: 0;
  font-family: Arial, sans-serif;
  transition: background-color 0.3s ease;
}

.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  transition: all 0.3s ease;
}

.header {
  background-color: #e4ecf5;
  padding: 1rem;
  transition: all 0.3s ease;
}

.header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav {
  margin-top: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}

.main {
  display: flex;
  flex: 1;
  min-height: 70vh;
  transition: all 0.3s ease;
}

.left, .right {
  width: 20%;
  background-color: #f4f4f4;
  padding: 1rem;
  transition: all 0.3s ease;
}

.center {
  width: 60%;
  padding: 2rem;
  background-color: #fff;
  transition: all 0.3s ease;
}

.footer {
  background-color: #d0dfea;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.section-nav p, .tags span {
  margin-bottom: 0.5rem;
}

.section-banner img,
.tag-banner img {
  width: 100%;
  max-width: 180px;
  margin: 10px auto;
  display: block;
  border-radius: 6px;
  transition: transform 0.3s ease;
}

.section-banner img:hover,
.tag-banner img:hover {
  transform: scale(1.05);
}

@media screen and (max-width: 800px) {
  .main {
    flex-direction: column;
  }

  .left, .center, .right {
    width: 100%;
  }

  .nav {
    flex-direction: column;
  }
}

.page h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.page p {
  font-size: 1.2rem;
  line-height: 1.6;
}

/* Общие стили */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #ffffff;
  color: #333333;
}

.header, .footer {
  background-color: #e6f0fa;
  padding: 1rem;
  text-align: center;
}

.logo-img {
  max-width: 80px;
  height: auto;
}

.nav a {
  margin: 0 1rem;
  text-decoration: none;
  font-weight: bold;
  color: #003366;
}

.nav select {
  margin-left: 1rem;
  padding: 0.25rem;
}

.page {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  padding: 1rem;
  max-width: 1920px;
  margin: auto;
}

aside.banner, aside.tags {
  flex: 1;
  background-color: #f0f8ff;
  padding: 1rem;
  border-radius: 10px;
  min-width: 150px;
}

main.content {
  flex: 3;
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 10px;
}

.banner-img, .ad-img, .logo-img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
}

footer {
  background-color: #dce9f9;
  text-align: center;
  padding: 1rem;
  margin-top: 2rem;
}

/* Медиа-запросы под разные разрешения */
@media (max-width: 1440px) {
  .nav a {
    margin: 0 0.5rem;
  }
}

@media (max-width: 1280px) {
  .page {
    flex-direction: column;
    align-items: center;
  }
  aside.banner, aside.tags, main.content {
    width: 90%;
  }
}

@media (max-width: 768px) {
  .nav {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .nav a, .nav select {
    margin: 0.5rem 0;
  }
  .page {
    padding: 0.5rem;
  }
}

@media (max-width: 480px) {
  .logo-img {
    max-width: 60px;
  }
  .page {
    padding: 0.25rem;
  }
  aside.banner, aside.tags, main.content {
    width: 100%;
    padding: 0.5rem;
  }
  .banner-img, .ad-img {
    border-radius: 5px;
  }
}
